


































































	export default {
		data() {
			return {
				title: 'video',
				src: '',
// 				danmuList: [{
// 						text: '第 1s 出现的弹幕',
// 						color: '#ff0000',
// 						time: 1
// 					},
// 					{
// 						text: '第 3s 出现的弹幕',
// 						color: '#ff00ff',
// 						time: 3
// 					}
// 				],
				// danmuValue: '',
				banner:{},
				type:2,//video
				defaultImgUrl:"../../../../static/img/headImage.png",
				commentList:[],
				pageNum:5,//评论数
				pageStart:0,//评论页数
				text:'',//评论内容
				haveMoreComment:true
			}
		},
		onLoad(event) {
			 // 目前在某些平台参数会被主动 decode，暂时这样处理。
			try {
			    this.banner = JSON.parse(decodeURIComponent(event.detailDate));
			} catch (error) {
			    this.banner = JSON.parse(event.detailDate);
			}
			// console.log(this.banner)
			 this.getDetail();
			uni.setNavigationBarTitle({
			    title: this.banner.name
			});
			this.getCommentList();
		},
		onReady: function(res) {

			this.videoContext = uni.createVideoContext('myVideo')

		},
		methods: {
			sendDanmu: function() {
				this.videoContext.sendDanmu({
					text: this.danmuValue,
					color: this.getRandomColor()
				});
				this.danmuValue = '';
			},
			videoErrorCallback: function(e) {
				uni.showModal({
					content: e.target.errMsg,
					showCancel: false
				})
			},
			getRandomColor: function() {
				const rgb = []
				for (let i = 0; i < 3; ++i) {
					let color = Math.floor(Math.random() * 256).toString(16)
					color = color.length == 1 ? '0' + color : color
					rgb.push(color)
				}
				return '#' + rgb.join('')
			},
			getDetail : function(){
				
			},
			getCommentList(){
				let that = this;
				let data = {
					type:that.type,
					targetId:that.banner.id,
					pageNum:that.pageNum,
					pageStart:that.pageStart
				};
				uni.request({
					url: this.websiteUrl+'uniApp/comment/getCommentListByIdAndType',
					data: data,
					success: (result) => {
						that.commentList = result.data.data.list;
					},
					fail: (data, code) => {
						uni.stopPullDownRefresh();
						console.log('fail' + JSON.stringify(data));
					}
				})
			},
			loadMoreComment(){
				this.haveMoreComment = false;
			},
			send(e) {}
		}
	}
